import { Swiper, Navigation } from 'swiper';

export default function() {
  if (!document.querySelector(".js-community-slider")) {
    return;
  }

  Swiper.use([Navigation]);

  const swiper = new Swiper(".js-community-slider", {
    slidesPerView: 1,
    spaceBetween: 200,
    loop: true,
    loopedSlides: 1,
    navigation: {
      nextEl: document.querySelector(".community-slider__arr-right"),
      prevEl: document.querySelector(".community-slider__arr-left"),
    },
  });
}
