import { Swiper, Pagination } from "swiper";

export default function () {
  if (!document.querySelector(".js-resource-center-slider")) {
    return;
  }

  const swiper = new Swiper(".js-resource-center-slider", {
    slidesPerView: 1,
    spaceBetween: 15,
    pagination: {
      el: ".swiper-pagination",
    },
  });
}
